import React, { useState, useEffect } from 'react';
import { Heart, ArrowLeft, Trophy, Wand2, Smartphone } from 'lucide-react';
import SafeImage from '../../SafeImage';

const PbtiSection = ({scrollToSection, isMobile, styles}) => {

    const [pbtiInfo, setPbtiInfo] = useState([
        {index: 1, icon: '', code: "H", code_nm: "", score: 0, color: '#bbe4de', img: 'main_h.PNG'},
        {index: 2, icon: '', code: "D", code_nm: "", score: 0, color: '#bbe4de', img: 'main_d.PNG'},
        {index: 3, icon: '', code: "W", code_nm: "", score: 0, color: '#bbe4de', img: 'main_w.PNG'},
        {index: 4, icon: '', code: "I", code_nm: "", score: 0, color: '#bbe4de', img: 'main_i.PNG'},
        {index: 5, icon: '', code: "S", code_nm: "SNS", score: 0,  color: '#bbe4de', img: 'main_s.PNG'},
        {index: 6, icon: '', code: "P", code_nm: "", score: 0, color: '#bbe4de', img: 'main_p.PNG'},
        {index: 7, icon: '', code: "G", code_nm: "", score: 0, color: '#bbe4de', img: 'main_g.PNG'},
        {index: 8, icon: '', code: "V", code_nm: "", score: 0, color: '#bbe4de', img: 'main_v.PNG'}
    ]);
    const [currentImageIndex, setCurrentImageIndex] = useState(0);
    const [currentStep, setCurrentStep] = useState('intro'); // 'intro', 'test', 'result'
    const [currentQuestion, setCurrentQuestion] = useState(0);
    const [answers, setAnswers] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [pbtiResult, setPbtiResult] = useState(null); //  

    const questions = [
        {
            id: 1,
            question: "      ?",
            image: "/image/phone/pbti/question1.jpg",
            options: [
                {text: ",   SNS  ", code: "S"},
                {text: " ,   ", code: "W"},
                {text: "    ", code: "B"}
            ]
        },
        {
            id: 2,
            question: " /  ?",
            image: "/image/phone/pbti/question2.jpg",
            options: [
                {text: ",    ", code: "V"},
                {text: "  ,   ", code: "G"},
                {text: ",    ", code: "I"}
            ]
        },
        {
            id: 3,
            question: " / . \n      ?",
            image: "/image/phone/pbti/question3.jpg",
            options: [
                {text: "/  ", code: "W"},
                {text: "      ", code: "D"},
                {text: "AI     ", code: "H,I"}
            ]
        },
        {
            id: 4,
            question: " ! \n   ?",
            image: "/image/phone/pbti/question4.jpg",
            options: [
                {text: " ,   ", code: "V"},
                {text: "  ,    ", code: "P,S"},
                {text: "    .  !", code: "G"}
            ]
        },
        {
            id: 5,
            question: "      ?",
            image: "/image/phone/pbti/question5.jpg",
            options: [
                {text: "    ", code: "I"},
                {text: "   ,  ", code: "W"},
                {text: "   ", code: "S"}
            ]
        },
        {
            id: 6,
            question: "   . \n     ?",
            image: "/image/phone/pbti/question6.jpg",
            options: [
                {text: "     ", code: "P"},
                {text: "     ", code: "H"},
                {text: "   ", code: "D"}
            ]
        },
        {
            id: 7,
            question: "    . \n    ?",
            image: "/image/phone/pbti/question7.jpg",
            options: [
                {text: ", ,    ", code: "P"},
                {text: "  ", code: "I"},
                {text: "  ", code: "D"}
            ]
        },
        {
            id: 8,
            question: "    10% . \n   ?",
            image: "/image/phone/pbti/question8.jpg",
            options: [
                {text: ",    ", code: "V"},
                {text: ",   SNS", code: "S"},
                {text: " ", code: "G"}
            ]
        },
        {
            id: 9,
            question: "   , \n .  ?",
            image: "/image/phone/pbti/question9.jpg",
            options: [
                {text: " ", code: "P"},
                {text: "   ", code: "V"},
                {text: "  ", code: "H"}
            ]
        },
        {
            id: 10,
            question: "  ?",
            image: "/image/phone/pbti/question10.jpg",
            options: [
                {text: ",     ", code: "H"},
                {text: ",     ", code: "S"},
                {text: " .    ", code: "D"}
            ]
        },
        {
            id: 11,
            question: "  . \n   ?",
            image: "/image/phone/pbti/question11.jpg",
            options: [
                {text: "   ,  ", code: "H"},
                {text: ",      ", code: "W,I"},
                {text: ",     ", code: "G,V"}
            ]
        },
        {
            id: 12,
            question: "     , \n  ?",
            image: "/image/phone/pbti/question12.jpg",
            options: [
                {text: "     ", code: "G"},
                {text: "     ", code: "W"},
                {text: "      ", code: "H"}
            ]
        }
    ];

    const handleAnswerSelect = (selectedOption) => {
        const newAnswers = [...answers, selectedOption.code];
        setAnswers(newAnswers);

        if (currentQuestion < questions.length - 1) {
            setCurrentQuestion(currentQuestion + 1);
        } else {
            setCurrentStep('result');
        }
    };

    const getResultCode = () => {
        const score = [1, 1, 1.5, 1.5, 1, 1, 1, 1, 1, 2, 2, 1];

        answers.forEach((codeStr, index) => {
            const weight = score[index];
            const codes = codeStr.split(',');
            codes.forEach(code => {
                const type = pbtiInfo.find(t => t.code === code);
                if (type) {
                    type.score += weight;
                }
            });
        });

        pbtiInfo.sort((a, b) => {
            if (b.score === a.score) return a.index - b.index;
            return b.score - a.score;
        });

        return pbtiInfo[0].code;
    };

    const getResultPBTI = async (resultCode) => {
        setIsLoading(true);
        try {
            const response = await fetch(`http://www.phonezipsa.com/phone/pbti?pcode=${encodeURIComponent(resultCode)}`);
            const result = await response.json();
            // console.log(result);
            return result;
        } catch (e) {
            console.error("PBTI   :", e);
            return null;
        } finally {
            setIsLoading(false);
        }
    };

    const resetTest = () => {
        setCurrentStep('intro');
        setCurrentQuestion(0);
        setAnswers([]);
        setPbtiResult(null);
        setIsLoading(false);
        setPbtiInfo(prev =>
            prev
                .map(item => ({ ...item, score: 0 })) //  score 
                .sort((a, b) => a.index - b.index)   // index  
        );
        scrollToSection('pbti');
    };

    useEffect(() => {
        const fetchResult = async () => {
            if (pbtiResult === null) {
                const resultCode = getResultCode();
                const result = await getResultPBTI(resultCode);
                setPbtiResult(result);
            }
        };

        // intro  
        if(currentStep === 'intro'){
            //  
            const interval = setInterval(() => {
                setCurrentImageIndex((prevIndex) => (prevIndex + 1) % pbtiInfo.length);
            }, 2000); //
            return () => clearInterval(interval);
        }
        if(currentStep === 'result'){
            fetchResult();
        }
    }, [currentStep, pbtiResult, isLoading]);

    //    
    const renderIntroContent = () => (
        <>
            <div style={{
                textAlign: 'center',
                marginBottom: '40px',
                color: 'white'
            }}>
                <div style={{
                    display: 'inline-flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    width: isMobile ? '40px':'60px',
                    height: isMobile ? '40px':'60px',
                    background: 'rgba(255, 255, 255, 0.2)',
                    borderRadius: '50%',
                    marginBottom: '10px',
                }}>
                    <Wand2 style={{width: isMobile ? '20px':'30px', height: isMobile ? '20px':'30px'}}/>
                </div>
                <h1 style={{
                    fontSize: isMobile ? '20px' : '32px',
                    fontWeight: 'bold',
                    // marginBottom: '12px'
                }}>
                    ! <br/>    
                </h1>
            </div>

            <div style={{
                ...styles.contentBlock,
                backgroundColor: "#bbe4de"
            }}>
                <div style={{
                    position: 'relative',
                    width: '100%',
                    maxWidth: isMobile ? '350px' : '500px', //   
                    height: isMobile ? '350px' : '500px', // 500x500  
                    margin: '0 auto 30px', //  
                    borderRadius: '20px',
                    overflow: 'hidden',
                    // boxShadow: '0 12px 35px rgba(0,0,0,0.15)'
                }}>
                    {pbtiInfo.map((img, index) => (
                        <div
                            key={index}
                            style={{
                                position: 'absolute',
                                top: 0,
                                left: 0,
                                width: '100%',
                                height: '100%',
                                opacity: index === currentImageIndex ? 1 : 0,
                                transition: 'opacity 0.8s ease-in-out',
                                background: `linear-gradient(135deg, ${img.color}, ${img.color}dd)`, //    
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                flexDirection: 'column'
                            }}
                        >
                            {/*   */}
                            <img
                                src={`/image/phone/pbti/${img.img}`}
                                alt={img.code_nm}
                                style={{
                                    width: '100%',
                                    height: '100%',
                                    objectFit: 'contain',
                                    objectPosition: 'center'
                                }}
                            />

                            {/*     () */}
                            {/*<div style={{*/}
                            {/*    position: 'absolute',*/}
                            {/*    bottom: '60px', //   */}
                            {/*    left: '50%',*/}
                            {/*    transform: 'translateX(-50%)',*/}
                            {/*    color: 'white',*/}
                            {/*    textAlign: 'center',*/}
                            {/*    background: 'rgba(0,0,0,0.5)', //  */}
                            {/*    padding: '10px 20px',*/}
                            {/*    borderRadius: '25px',*/}
                            {/*    backdropFilter: 'blur(5px)'*/}
                            {/*}}>*/}
                            {/*    <div style={{*/}
                            {/*        fontSize: isMobile ? '16px' : '20px',*/}
                            {/*        fontWeight: 'bold'*/}
                            {/*    }}>*/}
                            {/*        {img.icon} {img.code_nm}*/}
                            {/*    </div>*/}
                            {/*</div>*/}
                        </div>
                    ))}

                    {/*/!*   *!/*/}
                    {/*<div style={{*/}
                    {/*    position: 'absolute',*/}
                    {/*    bottom: '20px',*/}
                    {/*    left: '50%',*/}
                    {/*    transform: 'translateX(-50%)',*/}
                    {/*    display: 'flex',*/}
                    {/*    gap: '10px'*/}
                    {/*}}>*/}
                    {/*    {pbtiInfo.map((_, index) => (*/}
                    {/*        <div*/}
                    {/*            key={index}*/}
                    {/*            style={{*/}
                    {/*                width: '10px',*/}
                    {/*                height: '10px',*/}
                    {/*                borderRadius: '50%',*/}
                    {/*                background: index === currentImageIndex ? 'white' : 'rgba(255,255,255,0.5)',*/}
                    {/*                transition: 'all 0.3s ease'*/}
                    {/*            }}*/}
                    {/*        />*/}
                    {/*    ))}*/}
                    {/*</div>*/}
                </div>

                {/*   */}
                {/*<div style={{*/}
                {/*    display: 'grid',*/}
                {/*    gridTemplateColumns: isMobile ? 'repeat(2, 1fr)' : 'repeat(4, 1fr)',*/}
                {/*    gap: isMobile ? '10px' : '15px',*/}
                {/*    marginBottom: '30px'*/}
                {/*}}>*/}
                {/*    {Array.isArray(pbtiInfo) && pbtiInfo.map((option) => (*/}
                {/*        <div*/}
                {/*            key={option.code} // index   */}
                {/*            style={{*/}
                {/*                background: option.color || '#ccc',*/}
                {/*                padding: isMobile ? '12px 8px' : '15px',*/}
                {/*                borderRadius: '10px',*/}
                {/*                textAlign: 'center',*/}
                {/*                color: 'white',*/}
                {/*                fontWeight: 'bold',*/}
                {/*                display: 'flex',*/}
                {/*                flexDirection: 'column',*/}
                {/*                alignItems: 'center',*/}
                {/*                gap: '8px',*/}
                {/*                boxShadow: '0 2px 8px rgba(0,0,0,0.2)'*/}
                {/*            }}*/}
                {/*        >*/}
                {/*            <div style={{fontSize: isMobile ? '20px' : '24px'}}>*/}
                {/*                {option.icon}*/}
                {/*            </div>*/}
                {/*            <div style={{fontSize: isMobile ? '11px' : '14px'}}>*/}
                {/*                {option.code_nm}*/}
                {/*            </div>*/}
                {/*        </div>*/}
                {/*    ))}*/}
                {/*</div>*/}

                {/*   */}
                <button
                    onClick={() => setCurrentStep('test')}
                    style={{
                        background: 'linear-gradient(45deg, #8fd6cc, #5eb7a5)',
                        color: 'white',
                        border: 'none',
                        padding: isMobile ? '16px 32px' : '18px 40px',
                        borderRadius: '25px',
                        fontSize: isMobile ? '16px' : '18px',
                        fontWeight: 'bold',
                        cursor: 'pointer',
                        transition: 'all 0.3s ease',
                        boxShadow: '0 8px 25px rgba(94, 183, 165, 0.3)',
                        width: isMobile ? '100%' : 'auto',
                        minWidth: '200px',
                        position: 'relative',
                        overflow: 'hidden'
                    }}
                    onMouseOver={(e) => {
                        e.currentTarget.style.transform = 'translateY(-2px) scale(1.02)';
                        e.currentTarget.style.boxShadow = '0 12px 35px rgba(94, 183, 165, 0.4)';
                    }}
                    onMouseOut={(e) => {
                        e.currentTarget.style.transform = 'translateY(0) scale(1)';
                        e.currentTarget.style.boxShadow = '0 8px 25px rgba(94, 183, 165, 0.3)';
                    }}
                >
                    <span style={{
                      position: 'relative',
                      zIndex: 1,
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'center',
                      gap: '8px'
                    }}>
                     
                    </span>
                </button>

                <div style={{fontSize: isMobile ? '12px' : '14px', color: '#666', marginTop: '20px'}}>
                      12  
                </div>
            </div>

        </>
    );

    //    
    const renderTestContent = () => {
        const question = questions[currentQuestion];
        const progress = ((currentQuestion + 1) / questions.length) * 100;

        return (
            <div style={styles.contentBlock}>
                {/*   */}
                <div
                    onClick={() => setCurrentStep('intro')}
                    style={{
                        background: 'rgba(255,255,255,0.8)',
                        border: 'none',
                        outline: 'none',
                        cursor: 'pointer',
                        color: '#666',
                        display: 'flex',
                        alignItems: 'center',
                        gap: '2px',
                        marginBottom: '10px',
                        boxShadow: 'none',          //   
                        WebkitTapHighlightColor: 'transparent' //   
                    }}
                    onMouseDown={(e) => e.preventDefault()} //   
                    onFocus={(e) => e.currentTarget.style.boxShadow = 'none'} //    
                    onBlur={(e) => e.currentTarget.style.boxShadow = 'none'}  //    
                >
                    <ArrowLeft size={isMobile ? 12 : 24}
                               style={{ margin: isMobile ? "4px" : "8px" }}/>
                    <span style={{fontSize: isMobile ? '10px' : '16px',}}></span>
                </div>

                {/*   */}
                <div style={{
                    background: '#f0f0f0',
                    borderRadius: '10px',
                    height: '8px',
                    marginBottom: '25px',
                    overflow: 'hidden'
                }}>
                    <div style={{
                        background: 'linear-gradient(45deg, #667eea, #764ba2)',
                        height: '100%',
                        width: `${progress}%`,
                        transition: 'width 0.5s ease',
                        borderRadius: '10px'
                    }}/>
                </div>

                {/*   */}
                <div style={{
                    fontSize: isMobile ? '14px' : '16px',
                    color: '#667eea',
                    marginBottom: '20px',
                    fontWeight: 'bold'
                }}>
                    {currentQuestion + 1}/12
                </div>

                {/*   */}
                <div style={{
                    border: '3px solid #667eea',
                    borderRadius: '25px',
                    padding: isMobile ? '20px 15px' : '30px 25px',
                    marginBottom: '25px',
                    background: 'white',
                    position: 'relative'
                }}>
                    {/*   */}
                    <div style={{
                        position: 'absolute',
                        top: '-12px',
                        left: '50%',
                        transform: 'translateX(-50%)',
                        background: 'white',
                        padding: '0 10px'
                    }}>
                        <Heart size={isMobile ? 16 : 20} color="#667eea" fill="#667eea"/>
                    </div>

                    {/*   */}
                    <div style={{
                        width: isMobile ? '200px' : '330px',
                        height: isMobile ? '200px' : '330px',
                        margin: '0 auto 15px',
                        borderRadius: '15px',
                        overflow: 'hidden',
                        border: '2px solid #f0f0f0',
                        background: '#f8f9fa'
                    }}>
                        <img
                            style={{
                                width: '100%',
                                height: '100%',
                                objectFit: 'cover'
                            }}
                            src={question.image}
                            alt={` ${question.id}`}
                        />
                    </div>

                    {/*   */}
                    <h3 style={{
                        fontSize: isMobile ? '12px' : '18px',
                        color: '#333',
                        marginBottom: '10px',
                        lineHeight: '1.4',
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center',
                        textAlign: 'center',
                        height: isMobile ? '44px' : '50px', //      
                    }}>
                        {question.question.split('\n').map((line, index) => (
                            <React.Fragment key={index}>
                                {line}
                                {index < question.question.split('\n').length - 1 && <br/>}
                            </React.Fragment>
                        ))}
                    </h3>
                </div>

                <div style={{
                    display: 'flex',
                    flexDirection: 'column',
                    gap: isMobile ? '12px' : '15px'
                }}>
                    {question.options.map((option, index) => (
                        <div
                            key={index}
                            onClick={() => handleAnswerSelect(option)}
                            style={{
                                background: index % 2 === 0 ?
                                    'linear-gradient(45deg, #667eea, #764ba2)' :
                                    'linear-gradient(45deg, #764ba2, #667eea)',
                                color: 'white',
                                border: 'none',
                                padding: isMobile ? '15px 20px' : '18px 25px',
                                borderRadius: '15px',
                                fontSize: isMobile ? '10px' : '16px',
                                fontWeight: 'bold',
                                cursor: 'pointer',
                                transition: 'all 0.3s ease',
                                boxShadow: '0 4px 15px rgba(102, 126, 234, 0.3)',
                                width: '100%'
                            }}
                            onMouseOver={(e) => {
                                e.currentTarget.style.transform = 'scale(1.02)';
                                e.currentTarget.style.boxShadow = '0 6px 20px rgba(102, 126, 234, 0.4)';
                            }}
                            onMouseOut={(e) => {
                                e.currentTarget.style.transform = 'scale(1)';
                                e.currentTarget.style.boxShadow = '0 4px 15px rgba(102, 126, 234, 0.3)';
                            }}
                        >
                            {option.text}
                        </div>
                    ))}
                </div>
            </div>
        );
    };

    //    
    const renderResultContent = () => {
        //   
        if (isLoading || !pbtiResult) {
            return (
                <div style={styles.contentBlock}>
                    <style>
                        {`
                        @keyframes spin {
                            from { transform: rotate(0deg); }
                            to { transform: rotate(360deg); }
                        }
                    `}
                    </style>
                    <div style={{
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        gap: '20px',
                        color: 'white'
                    }}>
                        <div style={{
                            width: '50px',
                            height: '50px',
                            border: '5px solid rgba(255,255,255,0.3)',
                            borderTop: '5px solid white',
                            borderRadius: '50%',
                            animation: 'spin 1s linear infinite'
                        }}></div>
                        <p style={{fontSize: isMobile ? '16px' : '18px'}}>  ...</p>
                    </div>
                </div>
            );
        }

        const result = pbtiResult;
        return (
            <div style={styles.contentBlock}>
                {/*   */}
                <div style={{fontSize: '48px', margin: '-10px'}}>
                    <Trophy size={isMobile ? 25 : 40} color={result.pbti.color}/>
                </div>

                {/*   */}
                <h2 style={{
                    fontSize: isMobile ? '15px' : '24px',
                    color: result.pbti.color,
                    fontWeight: 'bold'
                }}>
                    {result.pbti.code_nm}
                </h2>

                {/*   */}
                <div style={{
                    width: isMobile ? '200px' : '300px',
                    height: isMobile ? '200px' : '300px',
                    margin: isMobile ? '15px auto' : '30px auto',
                    borderRadius: '20px',
                    overflow: 'hidden',
                    border: `4px solid ${result.pbti.color}`,
                    boxShadow: `0 8px 25px ${result.pbti.color}40`,
                    background: 'white'
                }}>
                    <img
                        src={`/image/phone/pbti/${result.pbti.img}`}
                        alt="PBTI  "
                        style={{
                            width: '100%',
                            height: '100%',
                            objectFit: 'cover'
                        }}
                    />
                </div>

                {/*    */}
                <div style={{
                    background: `${result.pbti.color}15`,
                    borderRadius: '20px',
                    padding: isMobile ? '15px' : '20px',
                    marginBottom: '25px'
                }}>
                    <h3 style={{
                        fontSize: isMobile ? '12px' : '18px',
                        color: result.pbti.color,
                        marginBottom: '15px'
                    }}>
                          
                    </h3>
                    <div
                        style={{
                            background: 'white',
                            padding: '15px',
                            borderRadius: '10px',
                            fontSize: isMobile ? '12px' : '14px',
                            color: '#333',
                            lineHeight: '1.5'
                        }}
                        dangerouslySetInnerHTML={{__html: result.pbti.desc}}
                    />
                </div>

                {/*    */}
                <div style={{
                    background: `${result.pbti.color}10`,
                    borderRadius: '15px',
                    padding: isMobile ? '15px' : '20px',
                    marginBottom: '25px'
                }}>
                    <Smartphone size={isMobile ? 15 : 24} color={result.pbti.color}/>
                    <h4 style={{
                        color: result.pbti.color,
                        marginBottom: '15px',
                        fontSize: isMobile ? '12px' : '18px'
                    }}>
                         
                    </h4>

                    {/*    */}
                    <div style={{
                        display: 'flex',
                        justifyContent: 'space-around',
                        gap: isMobile ? '10px' : '15px',
                        flexWrap: 'wrap'
                    }}>
                        {result.resultModel.map(([idx, model, modelKo, img]) => (
                            <div key={idx}>
                                <div style={{
                                    width: isMobile ? '50px' : '80px',
                                    height: isMobile ? '50px' : '80px',
                                    margin: '0 auto 10px',
                                    borderRadius: '8px',
                                    overflow: 'hidden',
                                    background: '#f8f9fa'
                                }}>
                                    <SafeImage
                                        src={img ? `/image/phone/model/${img}` : undefined}
                                        alt={modelKo}
                                        fallback="/image/phone/error.jpg"
                                        style={{
                                            width: '100%',
                                            height: '100%',
                                            objectFit: 'cover'
                                        }}
                                    />
                                </div>
                                <div style={{
                                    fontSize: isMobile ? '8px' : '12px',
                                    fontWeight: 'bold',
                                    color: '#333',
                                    textAlign: 'center'
                                }}>
                                    {modelKo}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

                {/*    */}
                <button
                    onClick={resetTest}
                    style={{
                        background: `linear-gradient(45deg, ${result.pbti.color}, ${result.pbti.color}dd)`,
                        color: 'white',
                        border: 'none',
                        padding: isMobile ? '12px 25px' : '15px 30px',
                        borderRadius: '25px',
                        fontSize: isMobile ? '12px' : '16px',
                        fontWeight: 'bold',
                        cursor: 'pointer',
                        transition: 'transform 0.3s ease',
                        boxShadow: `0 5px 15px ${result.pbti.color}40`,
                        width: isMobile ? '100%' : 'auto'
                    }}
                    onMouseOver={(e) => e.currentTarget.style.transform = 'scale(1.05)'}
                    onMouseOut={(e) => e.currentTarget.style.transform = 'scale(1)'}
                >
                     
                </button>
            </div>
        );
    };

    //    (currentStep  )
    const renderContent = () => {
        switch (currentStep) {
            case 'intro':
                return renderIntroContent();
            case 'test':
                return renderTestContent();
            case 'result':
                return renderResultContent();
            default:
                return renderIntroContent();
        }
    };

    return (
        <section id="pbti" style={styles.sectionBlock}>
            <div style={styles.containerBlock}>
                {renderContent()}
            </div>
        </section>
    );

};

export default PbtiSection;