import React from 'react';
import { Routes, Route } from 'react-router-dom';

//  import 
import App from './App';
import Phone from './phonedemonium/pages/PhoneMain';  //   

function Router() {
    return (
        <Routes>
            <Route path="/" element={<App />} />
            <Route path="/phone" element={<Phone />} />
        </Routes>
    );
}

export default Router;
